import React from "react";
import web1 from "../src/images/lotushotel.jpg"
import web2 from "../src/images/sachintraders.png"
import web3 from "../src/images/kundansweets.png"
import web4 from "../src/images/sanskarayush.png"
import web5 from "../src/images/streetviewlounge.png"
import web6 from "../src/images/laxmiherbals.png"
import { NavLink } from "react-router-dom";

const  Clients= () => {
  return (<>
  <div className= "my-5">
    <h1 className = "text-center"> Our Clients</h1>
  </div>
  
  <div id="carouselExampleCaptions" className="carousel slide col-5 mx-auto pb-5" data-ride="carousel">
  <ol className="carousel-indicators">
    <li data-target="#carouselExampleCaptions" data-slide-to="0" className="active"></li>
    <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
    <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
    <li data-target="#carouselExampleCaptions" data-slide-to="3"></li>
    <li data-target="#carouselExampleCaptions" data-slide-to="4"></li>
    <li data-target="#carouselExampleCaptions" data-slide-to="5"></li>
  </ol>
  <div className="carousel-inner">
    <div className="carousel-item active">
      <img src={web1} className=" img-fluid d-block w-100 img-thumbnail" alt={web1}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div className="carousel-item">
      <img src={web2} className="d-block img-fluid w-100" alt={web2}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div className="carousel-item">
      <img src={web3} className="img-fluid d-block w-100" alt={web3}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div className="carousel-item ">
      <img src={web4} className="img-fluid d-block w-100" alt={web4}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div className="carousel-item">
      <img src={web5} className="img-fluid d-block w-100" alt={web5}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
    <div className="carousel-item">
      <img src={web6} className="img-fluid d-block w-100" alt={web6}/>
      <div className="carousel-caption d-none d-md-block">
      </div>
    </div>
  </div>
  <NavLink className="carousel-control-prev" to="#carouselExampleCaptions" role="button" data-slide="prev">
    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
    <span className="sr-only">Previous</span>
  </NavLink>
  <NavLink className="carousel-control-next" to="#carouselExampleCaptions" role="button" data-slide="next">
    <span className="carousel-control-next-icon" aria-hidden="true"></span>
    <span className="sr-only">Next</span>
  </NavLink>
</div>


</>
);
};
export default Clients;
