import React from "react";
import web1 from "../src/images/b1.png"
import { NavLink } from "react-router-dom";

const Blog = () => {
  return (<>
<div className="container my-5">
          <div className="row d-flex justify-content-center">
              <div className="col-12">
                <div id="carousel" className="carousel slide" data-ride="carousel">
                    <ol className="carousel-indicators">
                        <li data-target="#carousel" data-slide-to="0" className="active bg-secondary"></li>
                        <li data-target="#carousel" data-slide-to="1" className="bg-secondary"></li>
                        <li data-target="#carousel" data-slide-to="2" className="bg-secondary"></li>
                      </ol>
                    <div className="carousel-inner">
                      <div className="carousel-item active">
                        <div className="row">
                            <div className="col-12 col-md-6 col-xl-3 mb-4">
                                <div className="card mr-3">
                                    <img src={web1} className="card-img-top" alt="b1"/>
                                    <div className="card-body">
                                      <h5 className="card-title">Creating a Conda Installer for NILMTK during SRIP @ IIT- GN</h5>
                                      <p className="card-text">The Non-Intrusive Load Monitoring Toolkit (NILMTK) helps in analyzing energy data collected in different formats by converting it to a standardized NILMTK-DF using dataset parsers and then providing benchmarking disaggregation algorithms plus comparing the performances of those algorithms using accuracy metrics.</p>
                                      <NavLink to="https://sustainability-lab.github.io/2019/05/19/creating-conda-installer.html" target="_blank" className="btn btn-primary">Read more</NavLink>
                                    </div>
                                </div>
              </div>
              </div>
              </div>
              </div>
              </div>
              </div>
          
              </div>
      </div> </>   );
};

export default Blog;