import React from "react";
import { NavLink } from "react-router-dom";
const Cardservice = (props) => {
  return (
    <>
<div className="card mb-3 ">
  <div className="card-body">
    <h2 className="card-title text-center">{props.title}</h2>
    <h4><p className="card-text">{props.text}</p></h4>
  </div>
</div>
  <img className="card-img-bottom" src={props.imgsrc} alt="Alt-Img"/>
  <NavLink to="/contact" className="btn btn-primary">Get Your Quote</NavLink>

</>
);
};
export default Cardservice;