import React  from 'react';

class ProgressBar extends React.Component {
    render() {
      const p1 = 100;
      const p2 = 100;
      const p3 = 100;
      return (
              <div className = "col-md-10 mx-auto pt-5 pb-5">
            <div className = " ">
              <h1 class= "font-weight-bold text-center">We Have Provided</h1>
              </div>
          <h4 className = "text-center">Customer Satisfaction</h4>
          <div className="progressbar-container">
            <div className="progressbar-complete" style={{width: `${p1}%`}}>
              <div className="progressbar-liquid"></div>
            </div>
            <span className="progress font-weight-bold">{p1}%</span>
          </div>
       
          <h4 className = "text-center"> Satisfied Results </h4>
          <div className="progressbar-container">
            <div className="progressbar-complete" style={{width: `${p2}%`}}>
              <div className="progressbar-liquid"></div>
            </div>
            <span className="progress font-weight-bold">{p2}%</span>
          </div>
            
          <h4 className = "text-center">Happy Customers</h4>
          <div className="progressbar-container">
            <div className="progressbar-complete" style={{width: `${p3}%`}}>
              <div className="progressbar-liquid"></div>
            </div>
            <span className="progress font-weight-bold">{p3}%</span>
          </div>
                </div>

      )
    }
  }
  
  export default ProgressBar;
